import './resume.css';
import jsPDF from 'jspdf';
import html2canvas from "html2canvas";
import map from './map.png';
import phone from './phone-call.png';
import linkedin from './linkedin.png';
import email from './email.png';
import exp from './exp.png'
import edu from './edu.png'
import skills from './skills.png';
import React, { useState } from "react";
import cer from './cer.png';
function Resume({changeImgTwo}){


			
				const createPDF = async () => {   
					  const pdf = new jsPDF("portrait", "pt", "a4"); 
					  const data = await html2canvas(document.querySelector("#bodyR"));
					  const img = data.toDataURL("image/png");  
					  const imgProperties = pdf.getImageProperties(img);
					  const pdfWidth = pdf.internal.pageSize.getWidth();
					  const pdfHeight = (imgProperties.height * pdfWidth) / imgProperties.width;
					  pdf.addImage(img, "PNG", 0, 0, pdfWidth, pdfHeight);
					  pdf.save("resume.pdf");
					  document.getElementById("bodyR").style.backgroundColor = "white";
					};
					
					const [file, setFile] = useState();
					function handleChange(e) {
						// console.log(e.target.files);
						setFile(URL.createObjectURL(e.target.files[0]));
						document.getElementById("imgInTwo").style.display ="none";
						
					}

					// changeImgTwo=()=>{
					// 	document.getElementById("imgInTwo").style.display ="block";
					// }
					

	return(
			<div className='Resume'>

				<div className='headR'>
					<button onClick={createPDF} className='headB'>Save as pdf</button>
				</div> 
				<div className='resumeBody'>
						<div id='bodyR'>
							<div className='resumeTwo'>
								<div className='upperTwo'>
									<div className='leftUpperTwo'>
										<div className='photoTwo'>
											<div className="imgTwo">
												<img className='imgT'  src={file}/>
											</div>
											<input id='imgInTwo' name='fu' type='file' accept="image/png, image/jpeg" onChange={handleChange}  />
										</div>
										<div className='aboutTwo'>
											<p className='nameTwo' contentEditable='true' >
												Name
											</p>
											<p className='jobTitleTwo' contentEditable='true'>
												Job Title
											</p>
											<p className='bioTwo' contentEditable='true'>
												Write a short bio upto 60-70 words.	
											</p>

										</div>
									</div>
									<div className='rightUpperTwo'>
											<div className='contactImgsTwo'><img className='contactTwoImg' src={phone} /><p className='contactTextTwo' contentEditable="true" >0123456789</p></div> 
											<div className='contactImgsTwo' ><img className='contactTwoImg' src={email} /><p className='contactTextTwo' contentEditable="true" >mail@xyz.com</p></div>
											<div className='contactImgsTwo' ><img className='contactTwoImg' src={map} /><p className='contactTextTwo' contentEditable="true" >Location</p></div>
											<div className='contactImgsTwo' ><img className='contactTwoImg' src={linkedin} /><p className='contactTextTwo' contentEditable="true" >LinkedIn</p></div>
									</div>
								</div>
								<div className='lowerTwo'>
									<div className='leftLowerTwo'>
										<div className='expTwo'>
											<div className='headingTwo'>
												<img className='expImgTwo' src={exp}/>
												<h2 className='expHeadTextTwo'  >EXPERIENCE</h2>
											</div>
											<div className='expDetailsTwo expOneTwo'>
												<p className='jobTitleExpTwo' contentEditable="true">Job Title Here</p>
												<p className='companyNameExpTwo' contentEditable="true">Company Name Here</p>
												<p className='session' contentEditable="true"> Session Here</p>
												<div className='acTwo'>
													<p className='aTwo' contentEditable="true">Achievements Here upto 50 words</p>
													<p className='aTwo' contentEditable="true">Achievements Here</p>
													<p className='aTwo' contentEditable="true">Achievements Here</p>
													<p className='aTwo' contentEditable="true">Achievements Here</p>
												</div>
											</div>
											<div className='expDetailsTwo'>
												<p className='jobTitleExpTwo' contentEditable="true">Job Title Here</p>
												<p className='companyNameExpTwo' contentEditable="true">Company Name Here</p>
												<p className='session' contentEditable="true"> Session Here</p>
												<div className='acTwo'>
													<p className='aTwo' contentEditable="true">Achievements Here upto 50 words</p>
													<p className='aTwo' contentEditable="true">Achievements Here</p>
													<p className='aTwo' contentEditable="true">Achievements Here</p>
													<p className='aTwo' contentEditable="true">Achievements Here</p>
												</div>
											</div>
										</div>
										<div className='eduTwo'>
											<div className='headingTwo'>
													<img className='expImgTwo' src={edu}/>
													<h2 className='expHeadTextTwo'>EDUCATION</h2>
											</div>
											<div className='eduDetailsTwo eduDetailsOneTwo'>
													<p className='degTwo eduT' contentEditable="true">Degree Name Here</p>
													<p className='uniTwo eduT' contentEditable="true">University  Name</p>
													<p className='session eduT' contentEditable="true"> Session Here</p>
													
											</div>
											<div className='eduDetailsTwo'>
													<p className='degTwo eduT' contentEditable="true">Degree Name Here</p>
													<p className='uniTwo eduT' contentEditable="true">University  Name</p>
													<p className='session eduT' contentEditable="true"> Session Here</p>
													
											</div>
										</div>
										
									</div>
									<div className='rightLowerTwo'>
										<div className='skillsTwo'>
											<div className='headingTwo'>
												<img className='expImgTwo' src={skills}/>
												<h2 className='expHeadTextTwo'>SKILLS</h2>
											</div>
											<div className='skillDetailTwo'>
												<p className='skillTwo' contentEditable="true" >Skill</p>
												<p className='skillTwo' contentEditable="true" >Skill</p>
												<p className='skillTwo' contentEditable="true" >Skill</p>
												<p className='skillTwo' contentEditable="true" >Skill</p>
												<p className='skillTwo' contentEditable="true" >Skill</p>
												<p className='skillTwo' contentEditable="true" >Skill</p>
												<p className='skillTwo' contentEditable="true" >Skill</p>
												<p className='skillTwo' contentEditable="true" >Skill</p>
												<p className='skillTwo' contentEditable="true" >Skill</p>
												<p className='skillTwo' contentEditable="true" >Skill</p>
												<p className='skillTwo' contentEditable="true" >Skill</p>
					
												
												
												
							
											</div>
										</div>
										<div className='certificatesTwo'>
											<div className='headingTwo'>
												{/* <img className='expImgTwo' src={cer}/> */}
												<h2 className='expHeadTextTwo'></h2>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
				</div>
				
			</div>
		 );
}

export default Resume;