import './home.css';
import { BrowserRouter as Router,Routes, Route, Link } from 'react-router-dom';
import proud from './proud.svg';
import { Helmet } from "react-helmet";

function Home(){
	return(
			<div className='Home'>
				
				<div className='container home'>
					<div className='leftHome'>
						<h1 className='bodyTextH'>Welcome to Resume Hub,</h1>
						<div className='bodyText'>
							<h1 className='bodyTextHome'>
								Build a 100% free professional resume for yourself.
							</h1>
							<h3 className='bodyTextHomeSub'>
								We don't save any data!
							</h3>
							<h5 className='bodyTextHomeSubH'>
								Best view in laptop/computer
							</h5> 
							<center><Link style={{ textDecoration: 'none' }} to="/templates"><button className='build'><b>Build My Resume</b></button></Link></center>
						</div>
					</div>
					<div className='rightHome'>
						<img className='imgHome' src={proud} />
					</div>
				
				

				</div>
			</div>
		);
}

export default Home;